<template>
    <section class="receipt-section py-5">
        <div class="container">
            <div class="receipt-card shadow-lg rounded p-4 bg-white">

                <!-- Header -->
                <div class="text-center mb-4">
                    <h2 class="fw-bold text-primary">Wallet Recharge Receipt</h2>
                    <p class="text-muted">Thank you! Your transaction has been processed successfully.</p>
                    <hr />
                </div>

                <!-- Receipt Details -->
                <!-- <div class="row mb-3">
                    <div class="col-md-6">
                        <p class="mb-1 text-muted">Receipt No:</p>
                        <h6>{{ receipt.receiptNo }}</h6>
                    </div>

                    <div class="col-md-6 text-md-end">
                        <p class="mb-1 text-muted">Transaction Date:</p>
                        <h6>{{ receipt.date }}</h6>
                    </div>
                </div> -->

                <div class="row mb-4">
                    <div class="col-md-6">
                        <p class="mb-1 text-muted">Client Name:</p>
                        <h6>{{ ReceiptData.FirstName }}</h6>
                    </div>

                    <div class="col-md-6 text-md-end">
                        <p class="mb-1 text-muted">Transaction ID:</p>
                        <h6>{{ ReceiptData.TransactionID }}</h6>
                    </div>
                </div>

                <!-- Amount Box -->
                <div class="amount-box bg-light p-3 rounded mb-4 text-center">
                    <p class="text-muted mb-1">Recharge Amount</p>
                    <h2 class="text-success fw-bold">${{ ReceiptData.Amount }}</h2>
                </div>

                <!-- Payment Information -->
                <div class="row mb-4">
                    <div class="col-md-6">
                        <p class="mb-1 text-muted">Payment Method:</p>
                        <h6>Online</h6>
                    </div>

                    <div class="col-md-6 text-md-end">
                        <p class="mb-1 text-muted">Status:</p>
                         <h6 class="text-success">
                            {{ ReceiptData.Message }}
                        </h6> 
                    </div>
                </div>

                <hr />

                <!-- Footer -->
                <div class="text-center mt-3">
                    <button class="btn btn-primary px-4" @click="downloadReceipt">
                        Download Receipt
                    </button>

                    <button class="btn btn-outline-secondary px-4 ms-2" @click="goBack">
                        Back to Wallet
                    </button>
                </div>

            </div>
        </div>
    </section>
</template>

<script>
import DataService from "../../../services/DataService";

export default {
    data() {
        return {
            RechargeReceipt: null,
            ReceiptData: {}
        };
    },

    async created() {
        debugger
        var vm = this;
        vm.RechargeReceipt = vm.$route.params.TransactionId;
        vm.GetClientReceiptData();

    },

    methods: {
        downloadReceipt() {
            window.print(); // simple print download
        },
        goBack() {
            this.$router.push({ name: 'WalletRecharge' });
        },
        GetClientReceiptData() {
            debugger
            var vm = this;
            const url = `TransactionId=${vm.RechargeReceipt}`;
            DataService.GetClientReceiptData(url).then((response) => {

                if (response.data) {

                    vm.ReceiptData = response.data;

                }
                else {
                    vm.ReceiptData = {};
                }

            });
        },
    }
};
</script>

<style scoped>
.receipt-card {
    max-width: 650px;
    margin: auto;
}

.amount-box {
    border-left: 4px solid #28a745;
}

.receipt-section {
    background: #f7f9fc;
}
</style>
